<template>
	<view class="search-list">
		<view class="search-list-title">
			{{ title }}
		</view>
		<view class="search-item" v-for="(item,index) in value" :key="index">
			<view class="search-item_img">
				<image :src="item[srcName]" mode="aspectFill" class="search-item_img"></image>
			</view>
			<view class="search-item-name">
				<view class="search-item-name1">
					{{ item[titleName] }} 
				</view>
				<view class="search-item-name2" v-if="auxName">{{ item[auxName] }}</view>
			</view>
			<view class="search-item-button">
				<button type="default" :class="getClass" :hover-class="`search-item-button-${switchClass(item)}-hover`">{{ item[buttonName] }}</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		props:{
			title:{},
			value:{
				type:Array,
				default:()=>{
					return []
				}
			},
			// 指定类名渲染
			titleName:{
				type:String,
				default:"name"
			},
			// 指定类名渲染
			buttonName:{
				type:String,
				default:"title"
			},
			srcName:{
				type:String,
				default:"url"
			},
			// 这里接收一个函数主要传递
			switchClass:{
				type:Function
			},
			auxName:{
				type:String,
				default:null
			}
		},
		data(){
			return {
				
			}
		},
		computed:{
			getClass(){
				return `search-item-button-${switchClass(item)}`
			}
		}
	}
</script>

<style lang="scss">
	.search-list{
		padding: 20rpx;
		
	
	
		.search-list-title{
			font-size: 40rpx;
			font-weight: 600;
		}
		
		.search-item{
			display: flex;
			align-items: center;
			margin: 10rpx 0;
			.search-item_img{
				width: 100rpx;
				height: 100rpx;
				border-radius: $uni-border-radius-base;
			}
			
			.search-item-name{
				flex: 1;
				margin: 0 10rpx;
				white-space: nowrap;
				overflow: hidden;
			}
			
			.search-item-name1{
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
			
			.search-item-name2{
				font-size: 28rpx;
				color: rgb(155,155,155);
			}
			
			
			.search-item-button-default{
				border-radius: $uni-border-radius-lg;
				font-size: $uni-font-size-base;
				background-color: $uni-color-primary;
				color: #000000;
				line-height: $uni-font-size-base;
				padding: 10rpx 20rpx;
			}
			
			.search-item-button-default-hover{
				background-color: $uni-color-primary-hover;
			}
			
			
			.search-item-button-main{
				border-radius: $uni-border-radius-lg;
				font-size: $uni-font-size-base;
				background-color: rgb(234,246,253);
				color: #000000;
				line-height: $uni-font-size-base;
				padding: 10rpx 20rpx;
			}
			
			.search-item-button-main-hover{
				background-color:  rgb(226, 238, 244);
			}
			
		}
	}
</style>
